import {
	GOODSLIST,
	BEFORODER,
	CREATEORDER,
	MYINFO,
	COUPONLIST
} from "../../../utils/api.js"
export default {
	data() {
		return {
			modalName: null,
			saleItem: 0,
			levelModel: null,
			gridBorder: false,
			gridCol: 4,
			vipSku: "100001",
			goodslist: "",
			topCate: [{
					title: "智能代接",
					icon: "../../../static/mine/VIPPage/daijie@2x.png"
				},
				{
					title: "智能识别",
					icon: "../../../static/mine/VIPPage/shibie@2x.png"
				},
				{
					title: "个性回应",
					icon: "../../../static/mine/VIPPage/huiying@2x.png"
				},
				{
					title: "场景覆盖",
					icon: "../../../static/mine/VIPPage/fugai@2x.png"
				}
			],
			modalData: [{
					title: '智能代接',
					content: '骚扰来电、飞行、无服务、无网络模式开会、睡觉、玩游戏等不方便接听'
				},
				{
					title: '智能识别',
					content: '房产推销、股票推荐、贷款推销、教育推销、保险推销智能识别，一键拒接。'
				},
				{
					title: '个性回应',
					content: '自定开场白、回复音色；快递、外卖场景自定义回复'
				},
				{
					title: '场景覆盖',
					content: '快递、外卖、推销、广告、金融、催收'
				}
			],
			payPrice: '',
			savePrice: '',
			infoData: '',
			couponData: '',
			imageIndex: 0,
			couponId: '',
			now: "",
			couponList: '',
			transferData: '' //传递回来的数据
		}
	},
	computed: {
		getDueTime: function() {
			var time = this.infoData.vipEndTime
			var result = String(time).split(" ")[0]
			return result
		}
	},
	onLoad(options) {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			mask: true
		})
		if (options.data) {
			this.transferData = JSON.parse(options.data)
			this.couponId = this.transferData.id
			this.saleItem = this.transferData.item
			this.vipSku = this.transferData.sku
			console.log('是否有数据', this.transferData)
		}
		//卡列表
		this.getCard()
		// 优惠券
		this.getCoupons1()
		this.getCoupons()
		// 我的信息
		this.getMyInfo()
		this.getTime()
	},
	methods: {
		// 卡列表
		getCard() {
			const params = {}
			this.$request.url_request(GOODSLIST, params, "GET", res => {
				this.goodslist = JSON.parse(res.data).data
				this.payPrice = JSON.parse(res.data).data[0].priceDes
				this.savePrice = JSON.parse(res.data).data[0].price
			}, err => {})
		},
		// 卡密激活
		camolo() {
			uni.navigateTo({
				url: '../../camilo/camilo?type=2'
			})
		},
		// getTime
		getTime() {
			var myDate = new Date();
			var year = myDate.getFullYear();
			var month = myDate.getMonth() + 1;
			var date = myDate.getDate();
			var h = myDate.getHours();
			var m = myDate.getMinutes();
			var s = myDate.getSeconds();
			this.now = year + '-' + this.conver(month) + "-" + this.conver(date) + " " + this.conver(h) + ':' + this.conver(m) +
				":" + this.conver(s);
			console.log(this.now)
		},
		conver(s) {
			return s < 10 ? '0' + s : s;
		},
		// 获取我的信息
		getMyInfo() {
			const params = {}
			this.$request.url_request(MYINFO, params, "GET", res => {
				this.infoData = JSON.parse(res.data).data
				uni.hideToast()
			}, err => {})
		},
		gridClick(index) {
			this.imageIndex = index
			this.modalName = "Image";
		},
		tipsCloseClick() {
			this.modalName = "";
		},
		checkSaleItem(index, sku, priceD, total) {
			console.log(sku)
			this.saleItem = index;
			this.vipSku = sku
			this.couponId = ''
			this.getCoupons1()
			this.getCoupons()
			this.payPrice = priceD
			this.savePrice = total

		},
		// 
		getCoupons1() {
			const params = {
				sku: this.vipSku
			}
			this.$request.url_request(COUPONLIST, params, 'GET', res => {
				this.couponList = JSON.parse(res.data).data
			}, err => {})
		},
		// 获取优惠券信息
		getCoupons() {
			const params = {
				sku: this.vipSku,
				couponId: this.couponId
			}
			this.$request.url_request(BEFORODER, params, "POST", res => {
				this.couponData = JSON.parse(res.data).data
				console.log(this.couponData)
				if (this.couponData.userCoupon !== null) {
					this.couponId = JSON.parse(res.data).data.userCoupon.id
				}
			}, err => {})
		},
		goback() {
			uni.navigateBack({
				delta: 1
			})
		},
		// 优惠券详情
		couponsDetail() {
			var data = {
				sku: this.vipSku,
				item: this.saleItem
			}
			uni.navigateTo({
				url: "../Coupons/Coupons?data=" + JSON.stringify(data)
			})
		},
		// 支付
		vipPay() {
			console.log("优惠券id", this.couponId)
			const params = {
				sku: this.vipSku,
				couponId: this.couponId
			}
			this.$request.url_request(CREATEORDER, params, "POST", res => {
				this.payData = JSON.parse(res.data)
				if (typeof WeixinJSBridge == "undefined") {
					if (document.addEventListener) {
						document.addEventListener('WeixinJSBridgeReady', this.onBridgeReady, false);
					} else if (document.attachEvent) {
						document.attachEvent('WeixinJSBridgeReady', this.onBridgeReady);
						document.attachEvent('onWeixinJSBridgeReady', this.onBridgeReady);
					}
				} else {
					this.onBridgeReady();
				}

			}, err => {})
		},
		//  支付
		onBridgeReady() {
			WeixinJSBridge.invoke(
				'getBrandWCPayRequest', {
					"appId": this.payData.appId, //公众号名称，由商户传入     
					"timeStamp": this.payData.timeStamp, //时间戳，自1970年以来的秒数     
					"nonceStr": this.payData.nonceStr, //随机串     
					"package": this.payData.packageValue,
					"signType": this.payData.signType, //微信签名方式：     
					"paySign": this.payData.paySign //微信签名 
				},
				function(res) {
					if (res.err_msg == "get_brand_wcpay_request:ok") {
						uni.reLaunch({
							url: '../MinePage'
						})
						// 使用以上方式判断前端返回,微信团队郑重提示：
						//res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
					}
				}
			)
		},
		toastTip() {
			uni.showModal({
				content: '太火爆啦！魔小秘今日注册名额已超限！优惠将于明日00:00重新开启！',
				showCancel: false
			});
		},
		greenChannel() {
			uni.report('vipGreen', '支付页面绿色通道')
			uni.navigateTo({
				url: '../../greenChannel/greenChannel'
			})
		},
		AutomaticRenewal(){
			uni.navigateTo({
				url:'../AutomaticRenewal/AutomaticRenewal'
			})
		}
	}
}
